.downmain {
  background-color: #F8C300;
  width: 100%;
  height: 20px;
  display: flex;
  justify-content: start;
  align-items: center; }

.button {
  width: 150px;
  height: 30px;
  border-radius: 8px;
  margin-left: 10px;
  border: 2px solid white;
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .button:hover {
    background-color: white; }

.spans {
  display: flex;
  align-items: center;
  margin-left: 4px;
  color: white;
  font-size: 20px; }
  .spans:hover {
    color: #F8C300; }

.download {
  display: flex;
  margin-left: 10px;
  color: white;
  font-size: 20px;
  font-weight: bold; }
  .download:hover {
    color: #F8C300; }
