.headerRow {
  background-color: #1A1A1A;
  min-height: 186px;
  width: 100%;
  margin-bottom: 35px; }

.main {
  padding: 25px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%; }

.logo {
  width: 75px;
  height: 75px;
  margin-bottom: 20px; }

.logodesc {
  display: block;
  text-align: center;
  color: white;
  font-size: 25px;
  font-weight: bold; }

.promotion {
  display: block;
  letter-spacing: 3px;
  text-align: center;
  color: white; }

.maintext {
  display: flex;
  justify-content: center;
  flex-direction: column; }

.contentMain {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  flex-direction: column; }

.logoMain {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.thank {
  display: block;
  color: #F8C300;
  font-size: 40px;
  font-weight: bold;
  width: 100%;
  text-align: center; }

.info {
  display: block;
  color: white;
  font-size: 20px;
  font-weight: 200;
  width: 100%;
  text-align: center; }
